/* Global Reset */

* {
  margin: 0;
  padding: 0;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  background-color: white;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: 100%;
}

h1 {
    margin-bottom: 1em;
}

p {
    margin: 1em 0;
}
